@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.top_header {
  display: flex;
  justify-content: space-between;
  padding: 2rem;
}
.top_header_container {
  width: 88%;
  margin: 40px 5%;
}
.virtual_card_flex {
  display: flex;
}
.virtual_btn {
  width: 7rem;
  height: 2rem;
  border: none;
  background: white;
  color: grey;
  border-radius: 4px;
  font-weight: bold;
  box-shadow: 2px 7px 16px -2px rgba(206, 205, 205, 0.75);
  -webkit-box-shadow: 2px 7px 16px -2px rgba(196, 196, 196, 0.75);
  -moz-box-shadow: 2px 7px 16px -2px rgba(182, 181, 181, 0.75);
}
nav {
  display: flex;
  justify-content: space-between;
  padding: 0 2.5rem;
}
nav ul {
  display: flex;
}
nav ul li {
  list-style: none;
}
.navLink-1 {
  text-decoration: none;
  margin-right: 1rem;
  color: grey;
  font-weight: bold;
  padding-bottom: 0.6rem;
}
.navLink {
  text-decoration: none;
  margin: 0 1rem;
  color: grey;
  font-weight: bold;
  padding-bottom: 0.6rem;
}
.navLink:hover,
.navLink-1:hover {
  border-bottom: 4px solid palevioletred;
  color: black;
  transition: 0.2s all ease;
}
.active_navbar {
  border-bottom: 4px solid palevioletred;
  color: black;
}
.navBar_icon {
  display: flex;
}
.navbar_line {
  width: 96%;
  height: 1px;
  background-color: grey;
  margin: 0.7rem 0 0 2rem;
}
@media only screen and (max-width: 800px) {
  .top_header_container {
    margin: 0 4%;
  }
}

.card_container {
  margin: 3% 7%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 35%;
  border-radius: 4px;
  float: left;
  box-shadow: 2px 7px 16px -2px rgba(206, 205, 205, 0.75);
  -webkit-box-shadow: 2px 7px 16px -2px rgba(196, 196, 196, 0.75);
  -moz-box-shadow: 2px 7px 16px -2px rgba(182, 181, 181, 0.75);
}
.top_part {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}
.top_part_1 h2 {
  margin-bottom: 0.3rem;
}
.top_part_1 p,
.top_part_2 p {
  color: grey;
  text-transform: capitalize;
  margin-bottom: 0.5rem;
}
.burn_icon {
  width: 3rem;
  height: 3rem;
  background: rgb(255, 237, 240);
  margin-left: 4rem;
  border-radius: 50%;
  margin-bottom: 0.5rem;
  box-shadow: 2px 7px 16px -2px rgba(206, 205, 205, 0.75);
  -webkit-box-shadow: 2px 7px 16px -2px rgba(196, 196, 196, 0.75);
  -moz-box-shadow: 2px 7px 16px -2px rgba(182, 181, 181, 0.75);
}
.bottom_part_1 p,
.bottom_part_2 p {
  text-transform: capitalize;
  margin-bottom: 0.5rem;
}

.burner {
  width: auto;
  padding: 0 0.5rem;
  font-size: 0.7rem;
  background: transparent;
  color: grey;
  border: 1px solid grey;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 2px;
}
.green_line {
  height: 8px;
  background: green;
  border-radius: 0 4px 4px 0;
}
.pink_line {
  height: 8px;
  background: rgb(255, 118, 141);
  border-radius: 4px 0 0 4px;
}
.pink_green_line {
  display: flex;
  margin: 0 1rem;
}
.bottom_part {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}
@media only screen and (max-width: 800px) {
  .main_container {
    display: flex;
    flex-wrap: wrap;
  }
  .card_container {
    width: 100%;
  }
}

.body_icon {
  display: flex;
  margin: 1rem 8rem 0 0;
  justify-content: right;
}

.searchBox {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.searchText {
  width: 0rem;
  border-radius: 5rem;
  border: none;
  outline: none;
  transition: all 0.3s linear;
}
.searchBox:hover > .searchText {
  width: 12rem;
  padding: 0.5rem;
  border: 1px solid gray;
  box-shadow: 0 2rem 2rem -1.5rem rgba(0, 0, 0, 0.5);
}
.searchBox:hover > .search_btn {
  background: #fff;
  color: gray;
}
.searchText:focus {
  outline: none;
}

.search_btn {
  position: absolute;
  color: gray;
  right: 1rem;
  display: grid;
  border-radius: 50%;
  place-items: center;
}

.all_filter {
  display: flex;
  justify-content: right;
  margin: 1rem 8rem 0 0;
}
.filter_icon_container {
  width: 5rem;
  background: rgb(245, 245, 245);
  color: gray;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding: 0.5rem;
}

.filter_box_container {
  height: auto;
  border-radius: 4px;
  box-shadow: 2px 7px 16px -2px rgba(206, 205, 205, 0.75);
  -webkit-box-shadow: 2px 7px 16px -2px rgba(196, 196, 196, 0.75);
  -moz-box-shadow: 2px 7px 16px -2px rgba(182, 181, 181, 0.75);
}
.filter_header {
  padding: 1rem 0 0 2rem;
}
.type {
  margin-bottom: 1rem;
}
.subs_burn_flex {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
}
.check_subs {
  display: flex;
}
.check_subs p {
  margin-left: 0.5rem;
  margin-top: -0.19rem;
}
.check_burner {
  display: flex;
  margin-right: 2rem;
}
.check_burner p {
  margin-left: 0.5rem;
  margin-top: -0.19rem;
}
.type_container {
  padding: 2rem 0 2rem 2rem;
}
.type_container p {
  color: gray;
}
.select_option {
  width: 95%;
  height: 2.5rem;
  background: rgb(236, 236, 236);
  color: gray;
  border: none;
  outline: none;
  font-family: "Ubuntu", sans-serif;
  padding: 0.5rem;
  border-radius: 4px;
  margin-top: 1rem;
}
.filter_container_btn {
  margin: 1rem 2rem 0 0;
  display: flex;
  justify-content: space-between;
}
.apply_btn {
  width: 10rem;
  height: 2rem;
  border: none;
  outline: none;
  background: rgb(255, 90, 118);
  color: white;
  font-family: "Ubuntu", sans-serif;
  border-radius: 4px;
  cursor: pointer;
}
.clear_btn {
  width: 10rem;
  height: 2rem;
  border: none;
  outline: none;
  background: white;
  color: black;
  font-family: "Ubuntu", sans-serif;
  cursor: pointer;
  border-radius: 4px;
  margin-left: 2rem;
  box-shadow: 2px 7px 16px -2px rgba(206, 205, 205, 0.75);
  -webkit-box-shadow: 2px 7px 16px -2px rgba(196, 196, 196, 0.75);
  -moz-box-shadow: 2px 7px 16px -2px rgba(182, 181, 181, 0.75);
}
.scroll_div {
  height: 70vh;
  overflow-y: scroll;
}

@media only screen and (max-width: 800px) {
  .body_icon {
    display: flex;
    margin: 1rem 8% 0 0;
    justify-content: right;
  }
  .filter_box_container {
    width: 100%;
  }
  .all_filter {
    display: flex;
    justify-content: right;
    margin: 1rem 8% 0 2rem;
  }
}

body {
  font-family: "Ubuntu", sans-serif;
}

